.accordion__question::before {
  position: absolute;
  content: "";
  top: 44%;
  right: 16px;
  height: 0.0625rem;
  width: 1.75rem;
  background: #fff;
  transform: rotate(180deg);
  transition: all 0.3s ease-in-out;
}
