.btn {
  /*矢印と下線の基点とするためrelativeを指定*/
  position: relative;
  /*形状*/
  display: inline-block;
  padding: 0 1.25rem;
  color: #274875;
  font-size: 1rem;
  line-height: 1;
  font-family: "Cardo", serif;
  font-weight: 700;
  letter-spacing: 0.1em;
}

.btn--white {
  color: #fff;
}

.btn--404 {
  font-size: 0.75rem;
}
@media screen and (min-width: 768px) {
  .btn--404 {
    font-size: 1rem;
  }
}

.btn::first-letter {
  text-transform: uppercase;
}

/*矢印と下線の形状*/
.btn::before {
  content: "";
  /*絶対配置で下線の位置を決める*/
  position: absolute;
  bottom: -0.875rem;
  left: 16%;
  /*下線の形状*/
  width: 8.375rem;
  height: 0.0625rem;
  background: #274875;
  /*アニメーションの指定*/
  transition: all 0.3s;
}

.btn--404::before {
  width: 6.4375rem;
}
@media screen and (min-width: 768px) {
  .btn--404::before {
    width: 7.875rem;
  }
}

.btn--white::before {
  background: #fff;
}

.btn::after {
  content: "";
  /*絶対配置で矢印の位置を決める*/
  position: absolute;
  bottom: -0.6875rem;
  right: -12%;
  /*矢印の形状*/
  width: 0.625rem;
  height: 0.0625rem;
  background: #274875;
  transform: rotate(35deg);
  /*アニメーションの指定*/
  transition: all 0.3s;
}

.btn--white::after {
  background: #fff;
}

/*hoverした際の移動*/
.btn:hover::before {
  left: 1.9375rem;
}

.btn:hover::after {
  right: -2.0625rem;
}

